// это старые иконки с основного сайта
@font-face {
  font-family: 'icomoon-old-site';
  src:  url('~fonts/icomoon-old-site/icomoon-old.eot?j5dd0u');
  src:  url('~fonts/icomoon-old-site/icomoon-old.eot?j5dd0u#iefix') format('embedded-opentype'),
    url('~fonts/icomoon-old-site/icomoon-old.woff?j5dd0u') format('woff'),
    url('~fonts/icomoon-old-site/icomoon-old.ttf?j5dd0u') format('truetype'),
    url('~fonts/icomoon-old-site/icomoon-old.svg?j5dd0u#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icomoon-old-"], [class*=" icomoon-old"],
[class^="icomoon-old-"]:before, [class*=" icomoon-old"]:before {
  /* use !important to prevent issues with browser extensions that change fonts */
  cursor: pointer;
  font-family: 'icomoon-old-site' !important;
  text-decoration: none;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
